import React from "react";
import ReactDOM from "react-dom";
import { RecoilRoot } from "recoil";
import App from "./App";
const redirectWithPrams = () => {
    const history = window.history;
    const location = new URL(window.location.href);
    const prams = location.searchParams;
    const path = prams.get("path");
    if (path != null) {
        const route = path.replace(/\.[^/.]+$/, "");
        const url = new URL(location.origin + route);
        history.pushState({}, "", url);
    }
};
class Root extends React.Component {
    constructor(props) {
        super(props);
        redirectWithPrams();
    }
    render() {
        return (React.createElement(React.StrictMode, null,
            React.createElement(RecoilRoot, null,
                React.createElement(App, null))));
    }
}
console.log("Hello!");
ReactDOM.render(React.createElement(Root, null), document.getElementById("root"));
